import Data from './language.json';

const Language = () => {
    return(
        <>
        { Data.map((language) => (
            <div key={language.id} className="resume-item">
                <h4>{language.bahasa}</h4>
                <h5>{language.level}</h5>
            </div>
        ))}
        </>
    )
}

export default Language;